
.PDC__ButtonWrapper{
    display: block;
    position: relative;
    font-size: 12px;
    width: 185px;
    margin: 10px;
    color: var(--light-blue);
    cursor: pointer;
    border: 2px solid var(--light-blue);
    background-color: transparent;
}

.PDC__ButtonWrapper--red{
    color: var(--light-red);
    border: 2px solid var(--light-red);
}

.PDC__ButtonWrapper--disabled{
    background-color: var(--very-light-gray);
    border: 2px solid var(--light-gray);
    color: var(--light-white);
    cursor: auto;
}

.PDC__ButtonWrapper:hover{
    color: var(--white);
}

.PDC__ButtonWrapper::after {
    content: "";
    background: -webkit-linear-gradient(top left, var(--light-blue), var(--dark-blue));
    background: linear-gradient(to bottom right, var(--light-blue), var(--dark-blue));
    opacity: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;   
}

.PDC__ButtonWrapper--red::after{
    background: -webkit-linear-gradient(top left, var(--light-red), var(--dark-red));
    background: linear-gradient(to bottom right, var(--light-red), var(--dark-red));
}

.PDC__ButtonWrapper--disabled::after{
    background: var(--very-light-gray);
}

.PDC__ButtonWrapper:hover::after {
    opacity: 1;
}

.PDC__ButtonWrapper--wave:hover::after{
    opacity: 0;
}

.PDC__Button{
    font-size: 12px;
    letter-spacing: 3px;
    color: black;
    border: none;
    text-transform: uppercase;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    background-color: inherit;
    width: 100%;
    pointer-events: none;
    padding: 15px;
    color: inherit;
}

.PDC__ButtonWrapper--large{
    font-size: 18px;
    width: 250px;
}

.PDC__ButtonWrapper--small{
    font-size: 10px;
    width: 125px;
}

.PDC__ButtonSVG{
    height: 44px;
    position: absolute;
    top: 0;
}

body{

    --light-blue: #95caff;
    --dark-blue: #75aaff;

    --light-red: #ff6677;
    --dark-red: #ff2244;

    --white: #f5f5ff;
    --very-light-gray: #aaaaae;
    --light-gray: #4B4F58;
    --dark-gray: #3B3F48;

    --primary:  var(--white);
    --secondary: var(--dark-gray);

    background-color: var(--secondary);
    color: var(--primary);
