.PDC__TextInputWrapper{
    margin: 30px 0;
}

.PDC__TextInput{
    background: transparent;
    border: none;
    border-bottom: 1px dashed var(--light-blue);
    font-style: italic;
    color: var(--primary);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    outline: none;
    letter-spacing: 2px;
    font-family: Raleway Light;

    width: 200px;
    height: 30px;
    font-size: 20px;
}

.PDC__TextInput:focus{
    border-bottom: 1px dashed var(--primary);
}

.PDC__TextInput:hover{
    border-bottom: 1px dashed var(--very-light-gray);
}

.PDC__TextInput--large{
    width: 300px;
    height: 40px;
    font-size: 24px;
}

.PDC__TextInput--small{
    width: 150;
    height: 20px;
    font-size: 16px;
}

.PDC__Modal{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
}

.PDC__Modal > div{
    border: 2px solid var(--primary);
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);  
    background-color: var(--secondary); 
    padding: 20px;
}
